import './App.css'
import '../normalize.css'
import React, { useState, useEffect } from 'react';
import { Button, TextField, Typography, AppBar, Toolbar } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
    title: {
        display: 'none',
        [theme.breakpoints.up('sm')]: {
          display: 'block',
        },
    }
}))

const App = () => {
    const classes = useStyles();
    const [cookie, setCookie] = useState('')
    const [token, setToken] = useState('')
    const [disabled, setDisabled] = useState(true)

    useEffect(() => {
        return cookie && token ? setDisabled(false) : setDisabled(true)
    }, [cookie, token])

    const request = async ({ cookie, token }) => {
        try {
            await axios.post('http://105.pncln.com/watchAll', { cookie, token })
        } catch (e) {
            throw new Error(e)
        }
    }

    return (
        <div>
            <AppBar position="static">
                <Toolbar>
                    <Typography className={classes.title} variant="h6" noWrap>
                        Phys105 Videos
                    </Typography>
                </Toolbar>
            </AppBar>
            <div className='container'>
                <div className='content'>
                    <Typography className={classes.title} variant="body1" noWrap>
                        We don't store your cookies / token!
                    </Typography>
                    <br /><br />
                    <form noValidate autoComplete="off" className='form'>
                        <TextField className='input' value={token} onChange={event => setToken(event.target.value)} id="token" label="Token" variant="outlined" />
                        <br /><br />
                        <TextField className='input' multiline value={cookie} onChange={event => setCookie(event.target.value)} id="cookie" label="Cookie" variant="outlined" />
                        <br />
                        <br />
                        <Button disabled={disabled} onClick={() => {
                            request({ cookie, token })
                        }} className='watch-button' variant='outlined'>Watch</Button>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default App